'use strict';

var React = require('react');
var styleHelper = require('../../helper/styleHelper');
var util = require('../../helper/util');

var Border = require('../common/Border');
var appTypes = require('../../types/appTypes');
var assetsHelper = require('../../helper/assetsHelper');
var LabelParts = require('../common/LabelParts');
var AutoSizeHelper = require('../common/AutoSizeHelper');
var VerticalScrollBar = require('../common/VerticalScrollBar');
var ColorImage = require('../common/ColorImage');

var NodeType = {None: 1, Expansion: 2, Close: 3, File: 4};

var TreeItem = React.createClass({
  propTypes: {
    selected: React.PropTypes.bool.isRequired,
    data: React.PropTypes.object.isRequired,
    itemHeight: React.PropTypes.number.isRequired,
    onClickEvent: React.PropTypes.func.isRequired
  },

  render: function () {
    var imgStyle;
    var textStyle;
    var imgSize;

    var backgroundStyle = {
      width: '100%',
      height: this.props.itemHeight,
      backgroundColor: this.props.selected ? this.props.selectedColor : undefined
    };

    imgSize = (this.props.iconSize && parseInt(this.props.iconSize, 10)) || 16;

    imgStyle = {
      float: 'left',
      width: imgSize,
      height: imgSize,
      marginLeft: 4 + this.props.data.hierarchy * 20,
      marginTop: (this.props.itemHeight - imgSize) / 2
    };

    textStyle = {
      lineHeight: this.props.itemHeight + 'px'
    };

    return (<div style={backgroundStyle} onClick={this.props.onClickEvent}>
      <div style={imgStyle}>
        {this.renderIcon(imgSize, this.props.iconColor || this.props.textColor)}
      </div>
      <label style={textStyle}>
        {util.Text.convertSpaceToNbsp(this.props.data.text)}
      </label>
    </div>);
  },
  renderIcon: function (size, color) {
    var parentSize = Math.round(size * 0.8);
    if (this.props.iconStyle === 'isArrow') {
      if (this.props.data.nodeType === NodeType.Close) {
        return this.renderIconArrow(parentSize, false, color);
      } else if (this.props.data.nodeType === NodeType.Expansion) {
        return this.renderIconArrow(parentSize, true, color);
      }
    } else { // isSquare 以及老版本的情况下
      if (this.props.data.nodeType === NodeType.Close) {
        return this.renderIconDir(parentSize, false, color);
      } else if (this.props.data.nodeType === NodeType.Expansion) {
        return this.renderIconDir(parentSize, true, color);
      }
    }
    return this.renderIconFile(size, color);
  },
  renderIconDir: function (size, expanded, color) {
    return (
      <svg width={size} height={size} viewBox="0 0 20 20">
        <rect x="1" y="1" width="18" height="18" stroke={color} fill="none" strokeWidth="2"/>
        <line x1="4" y1="10" x2="16" y2="10" stroke={color} strokeWidth="2"/>
        {expanded && <line x1="10" y1="4" x2="10" y2="16" stroke={color} strokeWidth="2"/>}
      </svg>
    )
  },
  renderIconFile: function (size, color) {
    return (
      <svg width={size} height={size} viewBox="0 0 20 20">
        <path d="M 1 1 L 8 0 L 8 8 L 16 8 L 16 19 L 0 19 Z M 8 0 L 16 8" strokeWidth="2" stroke={color} fill="none"/>
      </svg>
    )
  },
  renderIconArrow: function (size, expanded, color) {
    if (expanded) {
      return (
        <svg width={size} height={size} viewBox="0 0 20 20">
          <line x1="1" y1="1" x2="9" y2="9" stroke={color} strokeWidth="2"/>
          <line x1="9" y1="9" x2="18" y2="1" stroke={color} strokeWidth="2"/>
        </svg>
      );
    }
    return (
      <svg width={size} height={size} viewBox="0 0 20 20">
        {expanded || <line x1="1" y1="1" x2="9" y2="9" stroke={color} strokeWidth="2"/>}
        <line x1="1" y1="18" x2="9" y2="9" stroke={color} fill="none" strokeWidth="2"/>
        {expanded && <line x1="9" y1="9" x2="18" y2="18" stroke={color} strokeWidth="2"/>}
      </svg>
    );
  }
});

var Tree = React.createClass({

  _parseItemText: function (text) {
    var resultObj = {
      hierarchy: 0,
      nodeType: NodeType.None,
      text: ''
    };

    var length = text.length;
    var index = 0;
    var bracket = false;
    var ch;
    var isLoop = true;

    while ((index < length) && isLoop) {
      ch = text.charAt(index);
      switch (ch) {
      case '.':
        resultObj.hierarchy = resultObj.hierarchy + 1;
        break;
      case '[':
        bracket = true;
        break;
      case ']':
        bracket = false;
        isLoop = false;
        break;
      case '+':
        if (bracket) {
          resultObj.nodeType = NodeType.Expansion;
        } else {
          isLoop = false;
        }
        break;
      case '-':
        if (bracket) {
          resultObj.nodeType = NodeType.Close;
        } else {
          resultObj.nodeType = NodeType.File;
          isLoop = false;
        }
        break;
      default:
        isLoop = false;
      }
      index = index + 1;
    }

    resultObj.text = text.substring(index, text.length);

    return resultObj;

  },

  _getItemArray: function () {
    var resultArray = [];
    var textArray = util.Text.delimitText(util.Text.removeLastEmptyRow(styleHelper.getText(this.props)), util.Text.DM_RETURN);

    textArray.forEach(function (text) {
      resultArray.push(this._parseItemText(text.trim()));
    }.bind(this));

    return resultArray;
  },

  getInitialState: function () {
    return {
      selectedIndex: this.props.selectedIndex
    };
  },

  componentWillMount: function () {
    var size = AutoSizeHelper.getComAutoSize(this.props, '^p');
    this.itemHeight = Math.round(size.height + 2 * 7.8);
  },

  render: function () {
    var itemArray = this._getItemArray();
    var borderStyle = styleHelper.StyleUtil(this.props).text().border().size().base().background().style;
    return (<div style={borderStyle}>
      <div style={{
        height: '100%',
        width: this.props.verticalScrollbarShow ? (styleHelper.getWidth(this.props) - 16) : '100%'
      }}>
        {
          itemArray.map(function (item, index) {
            return (<TreeItem {...this.props}
                              key={index}
                              data={item}
                              selected={index === this.state.selectedIndex}
                              itemHeight={this.itemHeight}
                              onClickEvent={this._onClick.bind(this, null, index)}
            />);
          }.bind(this))
        }
      </div>
      {
        this.props.verticalScrollbarShow &&
        <VerticalScrollBar value={parseInt(this.props.verticalScrollbarProgress, 10)}/>
      }
    </div>);
  },

  _onClick: function (e, index) {
    this.setState({selectedIndex: index});
  },

  componentDidMount: function () {
    $(ReactDOM.findDOMNode(this)).bind('itemClick', this._onClick);
  }
});


module.exports = Tree;



