/*
 * 右侧 测试项目板块
 * */

'use strict';
var React = require('react');

var TestProjects = React.createClass({
  propTypes: {
    openApp: React.PropTypes.func.isRequired
  },
  render: function () {
    var projects = [
      'data/pocket.mp',
      'data/项目.mpbeta',
      'data/项目.mp',
      'data/Project.mp',
      'data/Project.mpbeta',
      'data/cprgjKoLVf.zip',
      'data/项目2222.mpbeta',
      'data/项目3333.mp',
      'data/cprc74Ermy.zip',
      'data/cprc74Ermy.mpbeta',
      'data/项目(1).mp',
      'data/表格.mpbeta',
      'data/Ph.Oliviers.mpbeta',
      'data/cprmvQ5O9g.zip',
      'data/cprjERNdtw.mp',
      'data/cpr7di2Iq0.mp',
      'data/试试效果.mp'
    ];

    return (
      <div className='projects-list'>
        <h4>测试项目</h4>
        <ul>
          {projects.map(function (project) {
            return (
              <li key={project} onClick={this.openApp.bind(this, project)}>{project.replace('data/', '')}</li>
            );
          }, this)}
        </ul>
      </div>
    )
  },
  openApp: function (app) {
    this.props.openApp(app);
  }
});

module.exports = TestProjects;



